.sc-news-home{margin-top:40px}.sc-news-home .title-home{font-family:'brandontext_bold', sans-serif;font-size:18px;text-transform:uppercase;margin:0;margin-bottom:15px}.sc-news-home .title-home a{display:inline-block;text-decoration:none;padding:5px 10px 5px 0;border-bottom:3px solid #00a651}.sc-news-home .tin{padding:10px}.sc-news-home .tin .bder{padding:6px;border:1px solid #ededed;box-shadow:-5px 5px 0 #f0f0f0;transition:all .5s}.sc-news-home .tin .bder:hover{border-color:#00a651}.sc-news-home .tin .img-hover{display:block;width:100%;overflow:hidden}.sc-news-home .tin .img-hover img{-moz-transform:scale(1);-o-transform:scale(1);-ms-transform:scale(1);-webkit-transform:scale(1);transform:scale(1);transition:all .3s}.sc-news-home .tin .img-hover img:hover{-moz-transform:scale(1.2);-o-transform:scale(1.2);-ms-transform:scale(1.2);-webkit-transform:scale(1.2);transform:scale(1.2)}.sc-news-home .tin .txt{padding:10px}.sc-news-home .tin h3{font-family:brandontext_bold, sans-serif;font-size:15px;font-weight:500;text-transform:uppercase;margin-top:5px}.sc-news-home .tin .post-by{color:#666;font-size:14px;font-style:italic;padding-bottom:10px}.sc-news-home .tin hr{border-top:1px solid #ddd;margin:0px -10px 10px}.sc-news-home .tin p{font-size:14px;color:#888}.sc-news-home .tin ul.status li{display:inline-block;margin-right:20px}.sc-news-home .tin ul.status li .fa{color:#00a651;margin-right:5px}
